/** @format */

// @flow

type Props = {
	name: string;
	placeholder: string;
	id: string | number;
	register: any;
	type?: string;
	style?: string;
	options: [];
};
export const Select = (props: Props) => {
	const { name, placeholder, style,  } = props;
	return (
		<div className='flex flex-col gap-2'>
			<label htmlFor={name} className='text-gray-600 text-lg'>
				{placeholder}
			</label>
			<select className={`border ${style} text-base p-2 outline-none`}>
				<option value=''>Select</option>
			</select>
		</div>
	);
};
