/** @format */

import { useState, useEffect } from 'react';
import {  useForm } from 'react-hook-form';
import { Input } from '~/components/Form/Input';
import { Tab } from '@headlessui/react';
import { yupResolver } from '@hookform/resolvers/yup';
import { codeVersionSchema } from '~/utils/schema';

function classNames(...classes: any) {
	return classes.filter(Boolean).join(' ');
}

export default function Dashboard() {
	const {
		register,
		handleSubmit,
		reset,
		formState: { errors },
	} = useForm({
		mode: 'onChange',
		resolver: yupResolver<any>(codeVersionSchema),
	});
	const [versionCodeCd, setVersionCodeCd] = useState<any>('');
	const [cdrError, setCDRError] = useState('');
	let [categories] = useState({
		'CD RDR SCAN': '',
		MANUAL: '',
	});

	const healthReport = [
		{
			responseCode: 'WSCEFB356LOQWC',
			serviceDate: '23/09/2003',
			firstName: 'Manoj',
			lastName: 'Pant',
			dob: '25/02/1994',
			age: 34,
			gender: 'Male',
			versionCode: 'SCFRT5656578NHQSAW',
			expiry: '18/09/2024',
		},
		{
			responseCode: 'QWERTYGH568HTG',
			serviceDate: '12/02/2013',
			firstName: 'Rahul',
			lastName: 'Singh',
			dob: '25/02/1991',
			gender: 'Male',
			age: 56,
			versionCode: 'SCFRT56578NKJK87612',
			expiry: '18/09/2027',
		},
	];
	const [healthCard, setHealtCard] = useState<any>({});

	const cdRDRInputChange = (e: any) => {
		setVersionCodeCd(e.target.value);
		console.log(e.target.value, 'event');
		if (e?.target?.value?.length == 12) {
			setCDRError('');
			setTimeout(() => {
				alert(versionCodeCd);
			}, 2000);
			setHealtCard(healthReport[0]);
		} else if (e.target.length != 12) {
			setCDRError('Invalid HC Version Code');
			setHealtCard({});
		}
	};

	const handleEnter = (e: any) => {
		if (versionCodeCd?.length == 12 && e?.key === 'Enter') {
			alert('Enter Pressed');
		}
		if (versionCodeCd?.length == 12 && e?.key === 'Escape') {
			alert('Escape Pressed');
		}
	};

	const tabChanged = () => {
		reset();
		setCDRError('');
		setHealtCard({});
	};

	useEffect(() => {
		console.log('version code value');
	}, [versionCodeCd]);

	const onSubmit = () => {
		console.log('assssssssssssssssss');
		setHealtCard(healthReport[1]);
	};
	return (
		<div className='mt-10'>
		 
				<h1 className='font-bold text-2xl'>Dashboard</h1>
				<div className='w-full px-2 py-6 sm:px-0'>
					<Tab.Group>
						<Tab.List className='flex space-x-1 bg-blue-400 p-1'>
							{Object.keys(categories).map((category) => (
								<Tab
									key={category}
									onClick={tabChanged}
									className={({ selected }) =>
										classNames(
											'w-full rounded-lg py-2.5 text-sm font-medium leading-5',
											// 'ring-white/60 ring-offset-2 ring-offset-blue-400 focus:outline-none focus:ring-2',
											selected
												? 'bg-white text-black shadow'
												: 'text-blue-100  hover:text-white'
										)
									}>
									{category}
								</Tab>
							))}
						</Tab.List>
						<Tab.Panels className=''>
							<Tab.Panel
								className={classNames(
									'rounded-xl bg-white outline-none'
									// 'ring-white/60 ring-offset-2 ring-offset-blue-400 focus:outline-none outline-none focus:ring-2'
								)}>
								<div className='border border-gray-400 p-4 outline-none'>
									<div className='flex flex-col mb-8 w-60 relative'>
										<label className='text-gray-600 text-sm text-left mb-2'>
											HC Version Code
										</label>

										<input
											type='text'
											name='code'
											id='code'
											maxLength={12}
											onKeyDown={handleEnter}
											onChange={cdRDRInputChange}
											className='bg-gray-50 border border-gray-300  text-gray-900 sm:text-sm focus:border-primary-600 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white'
										/>
										{cdrError && (
											<label className='ml-0 pt-2 absolute top-16 text-red-600 text-left justify-start  text-xs'>
												{cdrError}
											</label>
										)}
									</div>
								</div>
							</Tab.Panel>
							<Tab.Panel
								className={classNames(
									'rounded-xl bg-white'
									// 'ring-white/60 ring-offset-2 ring-offset-blue-400 focus:outline-none focus:ring-2'
								)}>
								<form onSubmit={handleSubmit(onSubmit)}>
									<div className='border border-gray-400 p-4'>
										<div className='flex flex-col gap-y-6'>
											<div className='flex gap-x-4'>
												<Input
													type='text'
													register={register}
													error={errors}
													id={'hc'}
													required={true}
													name='hc'
													placeholder='HC'
												/>
												<Input
													type='text'
													register={register}
													error={errors}
													id={'hc'}
													required={true}
													name='versionCode'
													placeholder='Version Code'
												/>
											</div>
											<button
												// onClick={validateHealthCard}
												type='submit'
												className='text-center text-white bg-blue-500 w-[20%] items-center py-2'>
												Validate
											</button>
										</div>
									</div>
								</form>
							</Tab.Panel>
						</Tab.Panels>
					</Tab.Group>
				</div>

				<section className='flex flex-col'>
					<div className='border border-gray-400 rounded-md overflow-hidden '>
						<h1 className='text-center font-medium text-base text-white bg-blue-500 py-2'>
							Health Report
						</h1>
						<div className='flex flex-col gap-y-2 p-4'>
							<div className='grid grid-cols-4'>
								<div>
									<h2 className='text-sm'>MOH Response Code</h2>
									<p className='text-sm'>{healthCard?.responseCode || '-'}</p>
								</div>
								<div>
									<h2 className='text-sm'>Last Service Date</h2>
									<p className='text-sm'>{healthCard?.serviceDate || '-'}</p>
								</div>
								<div>
									<h2 className='text-sm'>First Name</h2>
									<p className='text-sm'>{healthCard?.firstName || '-'}</p>
								</div>

								<div>
									<h2 className='text-sm'>Last Name</h2>
									<p className='text-sm'>{healthCard?.lastName || '-'}</p>
								</div>
							</div>

							<div className='grid grid-cols-4 pt-2 border-0 border-t-2 '>
								<div>
									<h2 className='text-sm'>DOB</h2>
									<p className='text-sm'>{healthCard?.dob || '-'}</p>
								</div>
								<div>
									<h2 className='text-sm'>Age</h2>
									<p className='text-sm'>{healthCard?.age || '-'}</p>
								</div>
								<div>
									<h2 className='text-sm'>Gender</h2>
									<p className='text-sm'>{healthCard?.gender || '-'}</p>
								</div>
								<div>
									<h2 className='text-sm'>Health Card</h2>
									<p className='text-sm'>{healthCard?.versionCode || '-'}</p>
								</div>
							</div>
							<div className='grid grid-cols-4 pt-2 border-0 border-t-2 '>
								<div>
									<h2 className='text-sm'>Version Code</h2>
									<p className='text-sm'>{healthCard?.versionCode || '-'}</p>
								</div>
								<div>
									<h2 className='text-sm'>Expiry Date</h2>
									<p className='text-sm'>{healthCard?.expiry || '-'}</p>
								</div>
							</div>
						</div>
					</div>
				</section>
			 
		</div>
	);
}
