/** @format */

import React from "react";

import { Input } from "~/components/Form/Input";
import { Select } from "~/components/Form/Select";
import { useForm } from "react-hook-form";
import { yupResolver } from "@hookform/resolvers/yup";
import { createOrganistionSchema } from "~/utils/schema";

export default function Create() {
  const {
    register,
    formState: { errors },
  } = useForm({
    mode: "onChange",
    resolver: yupResolver<any>(createOrganistionSchema),
  });

  return (
    <>
      <div className="flex flex-col bg-white px-4 border  mt-8">
        <h2 className="font-bold text-xl py-4">Create Users</h2>
        <form>
          <div>
            <div className="grid grid-cols-1 md:grid-cols-3 gap-x-4 gap-y-8 mb-6">
              <Select
                name="Organization"
                placeholder="Organization"
                id="Organization"
                type="text"
                options={[]}
                register={register}
              />
              <Select
                name="Provider"
                placeholder="Provider"
                id="provider"
                type="text"
                options={[]}
                register={register}
              />
              <Select
                name="department"
                placeholder="Department"
                id="department"
                type="text"
                options={[]}
                register={register}
              />
              <Select
                name="role"
                placeholder="Role"
                id="role"
                type="text"
                options={[]}
                register={register}
              />
              <Input
                name="firstName"
                placeholder="First Name"
                error={errors}
                required={true}
                id={"firstName"}
                type="text"
                register={register}
              />
              <Input
                name="middleName"
                placeholder="Middle Name"
                id={"middleName"}
                error={errors}
                required={true}
                type="text"
                register={register}
              />
              <Input
                name="lastName"
                placeholder="Last Name"
                id={"lastName"}
                error={errors}
                required={true}
                type="text"
                register={register}
              />
              <Input
                name="userName"
                placeholder="User Name"
                id={"userName"}
                error={errors}
                required={true}
                type="text"
                register={register}
              />
              <Input
                name="password"
                placeholder="Password"
                id={"password"}
                error={errors}
                required={true}
                type="text"
                register={register}
              />
              <Input
                name="street1"
                placeholder="Street1"
                error={errors}
                required={true}
                id={"street1"}
                type="text"
                register={register}
              />
              <Input
                name="street2"
                placeholder="Street2"
                id={"street2"}
                error={errors}
                required={true}
                type="text"
                register={register}
              />
              <Input
                name="city"
                placeholder="City"
                id={"city"}
                error={errors}
                required={true}
                type="text"
                register={register}
              />
              <Input
                name="provState"
                placeholder="Prov State"
                id={"provState"}
                error={errors}
                required={true}
                type="text"
                register={register}
              />
              <Input
                name="mobile"
                placeholder="Mobile"
                id={"mobile"}
                error={errors}
                required={true}
                type="text"
                register={register}
              />
              <Input
                name="email"
                placeholder="Email"
                id={"email"}
                error={errors}
                required={true}
                type="text"
                register={register}
              />
			    <Select
              name="country"
              placeholder="Country"
              id="country"
              type="text"
              options={[]}
              register={register}
            />
            </div>

          
          </div>
          <button className="bg-blue-400 py-2 text-base text-white w-[10%] float-right my-4 rounded-lg">
            Submit
          </button>
        </form>
      </div>
    </>
  );
}
