/** @format */


import { Input } from "~/components/Form/Input";
import { Select } from "~/components/Form/Select";
import { useForm } from "react-hook-form";
import { yupResolver } from "@hookform/resolvers/yup";
import { createOrganistionSchema } from "~/utils/schema";

export default function Create() {
  const {
    register,

    formState: { errors },
  } = useForm({
    mode: "onChange",
    resolver: yupResolver<any>(createOrganistionSchema),
  });
  return (
    <>
      <div className="flex flex-col bg-white mt-8">
        <h2 className="font-bold text-2xl py-4 mb-8 px-4 underline">Create Provider</h2>
        <form>
          <div>
            <div className="grid grid-cols-1 md:grid-cols-3 gap-x-4 gap-y-8 mb-6 px-8">
              <Input
                name="organisation"
                placeholder="Organisation"
                error={errors}
                required={true}
                id={"organisation"}
                type="text"
                register={register}
              />
              <Input
                name="providerType"
                placeholder="Provider Type"
                id={"providerType"}
                error={errors}
                required={true}
                type="text"
                register={register}
              />
              <Input
                name="specialization"
                placeholder="Specialization"
                id={"specialization"}
                error={errors}
                required={true}
                type="text"
                register={register}
              />
              <Input
                name="firstName"
                placeholder="First Name"
                id={"email"}
                error={errors}
                required={true}
                type="text"
                register={register}
              />
              <Input
                name="lastName"
                placeholder="Last Name"
                id={"lastName"}
                error={errors}
                required={true}
                type="text"
                register={register}
              />
              <Input
                name="FullName"
                placeholder="Full Name"
                id={"lastName"}
                error={errors}
                required={true}
                type="text"
                register={register}
              />

              <Input
                name="NoOfProvide"
                placeholder="No Of Provider"
                id={"NoOfProvide"}
                error={errors}
                required={true}
                type="text"
                register={register}
              />
              <Input
                name="userId"
                placeholder="User Id"
                id={"userId"}
                error={errors}
                required={true}
                type="text"
                register={register}
              />
              <Input
                name="password"
                placeholder="Password"
                id={"password"}
                error={errors}
                required={true}
                type="text"
                register={register}
              />

              <Input
                name="street1"
                placeholder="Street1"
                error={errors}
                required={true}
                id={"street1"}
                type="text"
                register={register}
              />
              <Input
                name="street2"
                placeholder="Street2"
                id={"street2"}
                error={errors}
                required={true}
                type="text"
                register={register}
              />
              <Input
                name="city"
                placeholder="City"
                id={"city"}
                error={errors}
                required={true}
                type="text"
                register={register}
              />
              <Input
                name="provState"
                placeholder="Prov State"
                id={"provState"}
                error={errors}
                required={true}
                type="text"
                register={register}
              />
              <Input
                name="mobile"
                placeholder="Mobile"
                id={"mobile"}
                error={errors}
                required={true}
                type="text"
                register={register}
              />
              <Input
                name="email"
                placeholder="Email"
                id={"email"}
                error={errors}
                required={true}
                type="text"
                register={register}
              />

              
<Select
                name="country"
                placeholder="Country"
                id="country"
                type="text"
                options={[]}
				register={register}
              />
            </div>
            </div>

           
        
          <button className="bg-blue-500 py-2 text-base text-white w-[10%] float-right my-4 rounded-lg mx-4">
            Submit
          </button>
        </form>
      </div>
    </>
  );
}
