/** @format */

import { useNavigate } from "@remix-run/react";
import { Table } from "~/components/Table";
import { OrganisationColumn, OrganisationData } from "~/utils/columns/columns";

export default function Index() {
	const navigate = useNavigate()

  return (
    <div className="mt-10">
      <div className="flex justify-between items-center">
        <h2 className="font-bold text-2xl py-4">Create Organisation</h2>
        <button className="bg-blue-500 text-white px-4 py-2 rounded-full" onClick={()=> navigate("/organisation/create")}>Add Organization</button>
      </div>
      <Table column={OrganisationColumn} data={OrganisationData}/>
    </div>
  );
}
