/** @format */
import { useNavigate } from '@remix-run/react';
import { ProviderColumn, ProviderData } from '~/utils/columns/columns';
import { Table } from '~/components/Table';

export default function Index() {
	const navigate = useNavigate()
	return (
		<>
			<div className='mt-10'>
			<div className='flex justify-between items-center mb-4'>
			<h1 className='font-bold text-2xl'>Provider</h1>
				<button className="bg-blue-500 text-white px-4 py-2 rounded-full" onClick={()=> navigate("/provider/create")}>Add Provider</button>
			</div>
				<Table column={ProviderColumn} data={ProviderData} />
			</div>
		</>
	);
}
