 
import {
 
  flexRender,
  getCoreRowModel,
  useReactTable,
} from "@tanstack/react-table";
 

 
 
export const Table = ({column , data}:any) => {
   
  const table = useReactTable({
    data,
    columns:column,
    getCoreRowModel: getCoreRowModel(),
  });

  return (
    <div className="p-2">
      <table className="w-full">
        <thead className="w-full bg-gray-200 p-4">
          {table.getHeaderGroups()?.map((headerGroup) => (
            <tr
              className="border-gray-400 uppercase border"
              key={headerGroup.id}
            >
              {headerGroup.headers?.map((header) => (
                <th key={header.id} className="border py-4">
                  {header.isPlaceholder
                    ? null
                    : flexRender(
                        header.column.columnDef.header,
                        header.getContext()
                      )}
                </th>
              ))}
            </tr>
          ))}
        </thead>
        <tbody className="w-full text-center items-center bg-white  uppercase">
          {table.getRowModel().rows?.map((row) => (
            <tr key={row.id} className="border">
              {row.getVisibleCells()?.map((cell) => (
                <td key={cell.id} className="border py-2 text-sm">
                  {flexRender(cell.column.columnDef.cell, cell.getContext())}
                </td>
              ))}
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
};
