/** @format */

import { Input } from '~/components/Form/Input';
import { useForm } from 'react-hook-form';
import { yupResolver } from '@hookform/resolvers/yup';
import { createOrganistionSchema } from '~/utils/schema';


export default function Create() {
	const {
		register,
		formState: { errors },
	} = useForm({
		mode: 'onChange',
		resolver: yupResolver<any>(createOrganistionSchema),
	});
	return (
		<div className='mt-10'>
			<h2 className="font-bold text-2xl py-4 underline">Create Organisation</h2>
			<div className='flex flex-col bg-white py-8 px-4 mb-8'>
			
				
				<form>
					<div>
						<div className='grid grid-cols-1 md:grid-cols-4 gap-x-4 gap-y-8 mb-6'>
							 
							<Input
								name='organisation'
								placeholder='Organisation'
								error={errors}
								required={true}
								id={'organisation'}
								type='text'
								register={register}
							/>
							<Input
								name='phone'
								placeholder='Phone'
								id={'phone'}
								error={errors}
								required={true}
								type='text'
								register={register}
							/>
							<Input
								name='mobile'
								placeholder='Mobile'
								id={'mobile'}
								error={errors}
								required={true}
								type='text'
								register={register}
							/>
							<Input
								name='email'
								placeholder='E-mail'
								id={'email'}
								error={errors}
								required={true}
								type='text'
								// register={register}
							/>

							{/* <Select
							name='category'
							placeholder='Category'
							id='category'
							type='text'
							options={[]}
							register={register}
						/> */}
						</div>
						<div className='grid grid-cols-1 md:grid-cols-4 gap-x-4 gap-y-4 mb-6'>
							{/* <Input
							type='text'
							register={register}
							error={errors}
							id={'hc'}
							required={true}
							name='hc'
							placeholder='HC'
						/> */}
							<Input
								name='street1'
								placeholder='Street1'
								error={errors}
								required={true}
								id={'street1'}
								type='text'
								register={register}
							/>
							<Input
								name='street2'
								placeholder='Street2'
								id={'street2'}
								error={errors}
								required={true}
								type='text'
								register={register}
							/>
							<Input
								name='city'
								placeholder='City'
								id={'city'}
								error={errors}
								required={true}
								type='text'
								register={register}
							/>
							<Input
								name='provState'
								placeholder='Prov State'
								id={'provState'}
								error={errors}
								required={true}
								type='text'
								// register={register}
							/>
						</div>

						 
					</div>
					<button className='bg-blue-500 py-2 text-base text-white w-[10%] float-right my-4 rounded-lg'>
						Submit
					</button>
				</form>
			</div>
		</div>
	);
}
