/** @format */

import { useNavigate } from '@remix-run/react';

const Login = () => {
	const navigate = useNavigate();
	const hospitalList = [
		{
			id: 1,
			name: 'Max Hospital',
		},
		{
			id: 2,
			name: 'Appolo Hospital',
		},
		{
			id: 3,
			name: 'Fortis Hospital',
		},
		{
			id: 4,
			name: 'Medanta Hospital',
		},
	];

	const login = () => {
		navigate('/dashboard');
	};

	return (
		<section className='bg-gray-50 dark:bg-gray-900'>
			<div className='flex flex-col items-center justify-center px-6 py-8 mx-auto md:h-screen lg:py-0'>
				<a
					href='#/'
					className='flex items-center mb-6 text-2xl font-semibold text-gray-900 dark:text-white'>
					{/* <img
            className="w-8 h-8 mr-2"
            src="https://flowbite.s3.amazonaws.com/blocks/marketing-ui/logo.svg"
            alt="logo"
          /> */}
				</a>
				<div className='w-full bg-white rounded-lg shadow dark:border md:mt-0 sm:max-w-md xl:p-0 dark:bg-gray-800 dark:border-gray-700'>
					<div className='p-6 space-y-4 md:space-y-6 sm:p-8'>
						<h1 className='text-xl font-bold leading-tight tracking-tight text-gray-900 md:text-2xl dark:text-white'>
							Sign in to your account
						</h1>
						<form className='space-y-4 md:space-y-6'>
							<div>
								<label
									htmlFor='hospital'
									className='block mb-2 text-sm font-medium text-gray-900 dark:text-white'>
									Provider
								</label>
								<select className=' border-gray-400 block w-full h-10 px-2 text-sm text-gray-800  border rounded-md focus:outline-none'>
									<option value=''>Select</option>
									{hospitalList?.map((option: any, index: any) => {
										return (
											<option value={`${option.id}`} key={`option-${index}`}>
												{option?.name}
											</option>
										);
									})}
								</select>
							</div>
							<div>
								<label
									htmlFor='user'
									className='block mb-2 text-sm font-medium text-gray-900 dark:text-white'>
									Username
								</label>
								<input
									type='email'
									name='email'
									id='email'
									className='bg-gray-50 border border-gray-300 text-gray-900 sm:text-sm rounded-lg focus:ring-primary-600 focus:border-primary-600 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500'
									placeholder='Username'
								/>
							</div>
							<div>
								<label
									htmlFor='password'
									className='block mb-2 text-sm font-medium text-gray-900 dark:text-white'>
									Password
								</label>
								<input
									type='password'
									name='password'
									id='password'
									placeholder='••••••••'
									className='bg-gray-50 border border-gray-300 text-gray-900 sm:text-sm rounded-lg focus:ring-primary-600 focus:border-primary-600 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500'
								/>
							</div>

							<button
								type='submit'
								onClick={() => login()}
								className='w-full text-white bg-blue-600 hover:bg-primary-700 focus:ring-4 focus:outline-none focus:ring-primary-300 font-medium rounded-lg text-sm px-5 py-2.5 text-center dark:bg-primary-600 dark:hover:bg-primary-700 dark:focus:ring-primary-800'>
								Sign in
							</button>
						</form>
					</div>
				</div>
			</div>
		</section>
	);
};

export default Login;
