/** @format */

type Props = {
	name: string;
	placeholder: string;
	id: string | number;
	register?: any;
	type?: string;
	style?: string;
	error?: string | any;
	required?: boolean;
};
export const Input: React.FC<Props> = ({
	name,
	placeholder,
	id,
	register,
	type,
	style,
	error,
	required,
}) => {
	return (
		<div className='flex flex-col gap-2 relative'>
			<label htmlFor={name} className='text-gray-600 text-lg'>
				{placeholder}
			</label>
			<input
				type='text'
				name={name}
				// {...register(name, { required })}
				className={`border ${style} text-base p-2 outline-none w-full`}
			/>
			{error[name] && (
				<p
					className='m-auto ml-0 absolute top-16 text-xs pt-2 text-red-600 text-left justify-start'
					data-error='data-error'>
					{error[name]?.message}
				</p>
			)}
		</div>
	);
};
